.contact-container {
    background-image: url('../../public/images/octoPainting.png'); /* Set your background image */
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    color: white; /* Text color for content on top of the background */
    padding: 20px; /* Add padding as needed */
  }
  
  .contact-header {
    text-align: center;
    padding: 20px;
  }
  
  .social-media-icons {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .social-media-icons a {
    text-decoration: none;
    color: white;
    margin: 0 10px;
    transition: color 0.3s ease-in-out;
  }
  
  .social-media-icons a:hover {
    color: #007bff; /* Change color on hover */
  }
  
